<template>
  <div style="padding: 20px">
    <el-row>
      <el-col :span="24">
        <div class="grid-content bg-purple">
          <el-input placeholder="请输入内容" v-model="searchValue" style="width: 20%">
            <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
          </el-input>
          <el-button type="primary" class="ml-10" @click="handleReset">重置</el-button>
        </div>
      </el-col>
    </el-row>
    <hr />
    <div class="table-container">
      <el-table v-loading="loading" ref="multipleTable" :default-sort="{ prop: 'approve', order: 'descending' }" highlight-current-row :data="currentTableData" tooltip-effect="dark" style="width: 100%" border>
        <el-table-column type="index" width="50" align="center" label="序号" />
        <el-table-column prop="report.companyBasicInfo.companyName" label="机构名称" show-overflow-tooltip />
        <el-table-column prop="report.reportInfo.projectName" label="任务名称" show-overflow-tooltip />
        <el-table-column prop="report.samplingInfo.monitorProject" label="监测项目" show-overflow-tooltip />
        <el-table-column prop="uploader" label="提交人" show-overflow-tooltip />
        <el-table-column prop="uptime" label="提交时间" show-overflow-tooltip />
        <el-table-column label="内容" align="center">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="showDetail(scope.row.id)">详情</el-button>
          </template>
        </el-table-column>
        <el-table-column label="审核情况" prop="approve" align="center">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.approve === 0" type="warning">待审核</el-tag>
            <el-tag v-else-if="scope.row.approve === 1" type="success">已通过</el-tag>
            <el-tag v-else type="info">未通过</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center" fixed="right">
          <template slot-scope="scope">
            <el-dropdown v-if="!scope.row.approve" @command="approve($event, scope.row.id)">
              <el-button type="primary" size="mini">审核</el-button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="1">通过</el-dropdown-item>
                <el-dropdown-item command="2">不通过</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <el-button size="mini" type="danger" @click="deleteReport(scope.row.id, scope.$index)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination background :page-size="10" style="float: right; margin-top: 30px" :current-page.sync="currentPage" @current-change="handleCurrentChange" layout="total, prev, pager, next, jumper" :total="tableData.length"> </el-pagination>
      <!-- 详情 -->
      <el-dialog :visible.sync="showReportDetail" v-if="currentReport.id">
        <el-descriptions title="机构基本信息" direction="vertical" :column="2" border>
          <el-descriptions-item label="机构名称">{{ currentReport.report.companyBasicInfo.companyName }}</el-descriptions-item>
          <el-descriptions-item label="机构代码">{{ currentReport.report.companyBasicInfo.companyCode }}</el-descriptions-item>
          <el-descriptions-item label="法人">{{ currentReport.report.companyBasicInfo.legalPersonName }}</el-descriptions-item>
          <el-descriptions-item label="联系人">{{ currentReport.report.companyBasicInfo.contactPerson }}</el-descriptions-item>
          <el-descriptions-item label="联系方式">{{ currentReport.report.companyBasicInfo.phone }}</el-descriptions-item>
          <el-descriptions-item label="地址">{{ currentReport.report.companyBasicInfo.adress }}</el-descriptions-item>
          <el-descriptions-item label="邮政编码">{{ currentReport.report.companyBasicInfo.postcode }}</el-descriptions-item>
          <el-descriptions-item label="机构类别">{{ currentReport.report.companyBasicInfo.companyType }}</el-descriptions-item> </el-descriptions
        ><br />
        <el-descriptions title="报备信息" direction="vertical" :column="2" border>
          <el-descriptions-item label="业务统一代码">{{ currentReport.report.reportInfo.businessCode }}</el-descriptions-item>
          <el-descriptions-item label="任务名称">{{ currentReport.report.reportInfo.projectName }}</el-descriptions-item>
          <el-descriptions-item label="业务编号">{{ currentReport.report.reportInfo.businessNumber }}</el-descriptions-item>
          <el-descriptions-item label="业务类型">{{ currentReport.report.reportInfo.businessType }}</el-descriptions-item>
          <el-descriptions-item label="受测单位">{{ currentReport.report.reportInfo.particiCompany }}</el-descriptions-item>
          <el-descriptions-item label="委托单位">{{ currentReport.report.reportInfo.entrastCompany }}</el-descriptions-item>
          <el-descriptions-item label="联系人">{{ currentReport.report.reportInfo.contactPerson }}</el-descriptions-item>
          <el-descriptions-item label="联系方式">{{ currentReport.report.reportInfo.phone }}</el-descriptions-item> </el-descriptions
        ><br />
        <el-descriptions title="采样信息" direction="vertical" :column="1" border>
          <el-descriptions-item
            ><img :src="url" style="width: 100%; height: 100%" v-if="url" />
            <span v-else>暂无照片</span>
          </el-descriptions-item>
          <el-descriptions-item label="监测项目">{{ currentReport.report.samplingInfo.monitorProject }}</el-descriptions-item>
          <el-descriptions-item label="时间">{{ currentReport.report.samplingInfo.duration }}</el-descriptions-item>
          <el-descriptions-item label="采样人员">{{ currentReport.report.samplingInfo.samplingPerson }}</el-descriptions-item>
          <el-descriptions-item label="备注">{{ currentReport.report.samplingInfo.remake }}</el-descriptions-item>
        </el-descriptions>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import {
  getAllReport,
  approveReport,
  deleteReport,
  searchReport
} from '@/api/report'
export default {
  name: 'ReportManage',
  data () {
    return {
      searchValue: '',
      loading: false,
      tableData: [],
      currentTableData: [],
      currentPage: 1,
      showReportDetail: false,
      currentReport: {},
      url: ''
    }
  },
  created () {
    this.getReportList()
  },
  methods: {
    async getReportList () {
      this.loading = true
      const data = await getAllReport()
      this.loading = false
      this.tableData = data.result
      this.currentTableData = data.result.slice(0, 10)
    },
    // 审核
    async approve (result, id) {
      const data = await approveReport(id, result)
      if (data.result.length) {
        this.currentTableData.find((item) => item.id === id).approve = +result
      } else {
        this.$message.error('服务器出现未知错误！')
      }
    },
    async search () {
      const data = await searchReport(this.searchValue)
      this.tableData = data.result
      this.currentTableData = data.result.slice(0, 10)
      this.currentPage = 1
    },
    handleReset () {
      this.searchValue = ''
      this.getReportList()
      this.currentPage = 1
    },
    // 展示详情
    showDetail (id) {
      this.currentReport = this.currentTableData.find((item) => item.id === id)
      if (this.currentReport.url) this.url = this.$url + this.currentReport.url
      this.showReportDetail = true
    },
    // 删除
    deleteReport (id, i) {
      this.$confirm('确认是否删除？', '警告！')
        .then(async () => {
          const data = await deleteReport(id)
          if (data.result.length) {
            this.$message.success('已删除')
            this.currentTableData.splice(i, 1)
          } else {
            this.$message.error('服务器出现未知错误,请稍后再试！')
          }
        })
        .catch(() => {
          this.$message.info('已取消')
        })
    },
    handleCurrentChange () {
      this.currentTableData = this.tableData.slice(
        (this.currentPage - 1) * 10,
        this.currentPage * 10
      )
    }
  }
}
</script>

<style lang="less" scoped>
.password {
  vertical-align: -10px;
}
/deep/ .el-input__suffix .el-input__suffix-inner {
  cursor: pointer !important;
}
